.summary-container {
  display: flex;
  position: absolute;
  overflow: hidden;
  background-color: white;
  flex-direction: column;
  justify-content: flex-end;
  border: 0.5px solid black;
  border-radius: 10px;
  width: 950px;
  height: 175px;
}

.blocks-container {
  overflow: hidden;
  width: 100%;
  display: flex;
  flex-direction: row;
  border-top: 0.5px solid black;
  align-items: flex-end;
  height: 70%;
}

.translate-block-container {
  flex-basis: 50%;
  width: 50%;
  height: 100%;
}

.translate-form {
  height: 60%;
}

.form-input {
  width: 100%;
  height: 100%;
  font-size: 20px;
}

input:focus,
select:focus,
textarea:focus,
form:focus,
button:focus {
  border: 0;
  outline: none;
}

form,
input {
  border: 0;
}

#second-container {
  border-left: 0.5px solid black;
  cursor: pointer;
}

input {
  padding: 0 9px;
}

input::placeholder {
  color: gray;
}

#output-res-exists {
  padding: 0 9px;
  color: black;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}


#output-res-non-exists {
  padding: 0 9px;
  color: gray;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.placeholder-exists-value {
  color: black;
}